@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --profile-picture-size: 48px;
    --icon-color: #777777;
}

.app {
    display: flex;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(#009788 200px, #d9dbd6 200px);
}

.icon-clickable:hover {
    cursor: pointer;
}
.chatbox {
    display: grid;
    grid-template-areas: "navigation details" "navigation chat" "search chat" "list chat" "list message";
    grid-template-columns: 35% auto;
    margin: 5vh 0px;
    width: 1300px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.4);
}
.chatbox>.details-section {
    grid-area: details;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top-right-radius: 3px;
    background-color: #eeeeee;
    border-left: 1px solid #e7e7e7;
}

.details-section .user {
    display: grid;
    grid-template-areas: "picture details";
    grid-template-columns: 70px auto;
    padding-left: 20px;
}

.details-section .user .profile-picture {
    grid-area: picture;
    height: var(--profile-picture-size);
    width: var(--profile-picture-size);
    border-radius: 50%;
}

.details-section .user .details {
    grid-area: details;
    display: flex;
    flex-direction: column;
}

.details-section .user .details .name {
    font-size: 1.2rem;
}

.details-section .user .details .last-seen {
    color: rgb(100, 100, 100);
}

.details-section .actions>svg {
    margin-right: 25px;
    color: var(--icon-color);
    font-size: 1.6rem;
}

.details-section .actions>svg:first-of-type {
    transform: scale(-1, 1) rotate(-45deg);
}
.chatbox-navigation-open {
    /* Navigation height of the open navigation state*/
    grid-template-rows: 90px calc(200px - 5vh - 90px) 80px auto 90px;
}

.chatbox-navigation-closed {
    /* Navigation height of the closed navigation state*/
    grid-template-rows: 90px 0px 80px auto 90px;
}

.chatbox-navigation-open .navigation-section {
    grid-area: navigation;
    display: flex;
    align-items: flex-end;
    background-color: #01bfa5;
}

.chatbox-navigation-open .navigation-section .back {
    padding-bottom: 15px;
    padding-left: 30px;
    color: white;
    font-size: 2rem;
}

.chatbox-navigation-open .navigation-section .new-chat {
    padding: 15px;
    padding-left: 25px;
    color: white;
    font-size: 1.5rem;
    font-weight: 700;
}

.chatbox-navigation-closed .navigation-section {
    grid-area: navigation;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top-left-radius: 3px;
    background-color: #eeeeee;
}

.chatbox-navigation-closed .navigation-section .profile-picture {
    margin-left: 25px;
    width: var(--profile-picture-size);
    height: var(--profile-picture-size);
    border-radius: 50%;
}

.chatbox-navigation-closed .navigation-section .actions>svg {
    margin-right: 25px;
    color: var(--icon-color);
    font-size: 1.6rem;
}
.chatbox .search-section {
    position: relative;
    grid-area: search;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fbfbfb;
    border-bottom: 1px solid #e7e7e7;
}

.chatbox .search-section label {
    position: absolute;
    display: flex;
    left: 40px;
}

.chatbox .search-section label>svg {
    color: #afafaf;
}

.chatbox .search-section input {
    padding: 12px;
    padding-left: 80px;
    border: 1px solid rgb(240, 240, 240);
    border-radius: 3px;
    width: 70%;
    background-color: white;
    outline: none;
}

.chatbox .search-section input:focus {
    border: 1px solid rgb(220, 220, 220);
}

.chatbox .search-section input::-webkit-input-placeholder {
    font-family: Roboto;
    font-size: 0.85rem;
    color: var(--icon-color);
}

.chatbox .search-section input:-ms-input-placeholder {
    font-family: Roboto;
    font-size: 0.85rem;
    color: var(--icon-color);
}

.chatbox .search-section input::placeholder {
    font-family: Roboto;
    font-size: 0.85rem;
    color: var(--icon-color);
}
.chatbox .list-section {
    grid-area: list;
    overflow-y: scroll;
    border-bottom-left-radius: 3px;
    background-color: white;
}

.chatbox .list-section .contact:last-of-type .details {
    padding-bottom: 0px;
    border: none;
}
.contact {
    display: grid;
    grid-template-areas: "picture details" "picture details";
    grid-template-columns: 80px auto;
    height: 70px;
    padding: 10px 0px;
    padding-left: 20px;
    background-color: white;
}

.contact:hover {
    cursor: pointer;
    background-color: #eeeeee;
}

.contact .profile-picture {
    grid-area: picture;
    height: 56px;
    width: 56px;
    border-radius: 50%;
}

.contact .details {
    display: flex;
    flex-direction: column;
    padding-bottom: 15px;
    border-bottom: 1px solid rgb(240, 240, 240);
}

.contact .name {
    grid-area: name;
    font-size: 1.2rem;
}

.contact .status {
    grid-area: status;
    color: rgb(100, 100, 100);
}
.chatbox .chat-section {
    grid-area: chat;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 20px 100px;
    background-color: #e4ddd7;
}
.message-container {
    display: flex;
}

.message-us {
    justify-content: flex-end;
}

.message-them {
    justify-content: flex-start;
}

.message-us .message {
    background-color: #dcf8c7;
}

.message-theirs .message {
    background-color: #eeeeee;
}

.message-container .message {
    position: relative;
    border-radius: 10px;
    padding: 12px 20px;
    margin: 5px 0px;
}

.message-container .message .timestamp {
    display: inline-block;
    transform: translate(5px, 5px);
    font-size: 0.8rem;
    color: rgb(100, 100, 100);
}

.message-us .message::after {
    content: " ";
    position: absolute;
    right: -10px;
    border-top: 10px solid transparent;
    border-left: 18px solid #dcf8c7;
    border-bottom: 10px solid transparent;
    top: 20px;
}

.message-theirs .message::after {
    content: " ";
    position: absolute;
    left: -10px;
    border-top: 10px solid transparent;
    border-right: 18px solid #eeeeee;
    border-bottom: 10px solid transparent;
    top: 20px;
}
.chatbox .message-section {
    grid-area: message;
    display: flex;
    align-items: center;
    border-bottom-right-radius: 3px;
    background-color: #f0efed;
}

.chatbox .message-section>svg {
    flex: 0.1 1;
    color: var(--icon-color);
    font-size: 2rem;
}

.chatbox .message-section>.message-input {
    flex: 0.8 1;
    border: none;
    border-radius: 5px;
    padding: 20px;
    outline: none;
    font-size: 1rem;
    font-family: Roboto;
}
